<template>
  <div class="tab-product" :style="{paddingBottom: isSelected ? '0' : '2.24rem',marginTop: (isSelected || isBottomBar) ? '.24rem' : '0'}">
    <van-sticky :offset-top="offsetTop" z-index="99">
      <div class="top-bar">
        <div class="bar-item" v-for="(item, index) in tabs" :key="index" @click="changeTab(index)">
          <div class="bar-text">
            {{item.tabName}}
            <div class="icon" v-if="index === 1">
              <img alt :src="`${$cdnImageUrl}/search/triangle_up_light.png` | imageToHttps" v-if="qs !== 'price'" />
              <img alt :src="`${$cdnImageUrl}/search/triangle_up_dark.png` | imageToHttps" v-if="qs === 'price'" />
              <img alt :src="`${$cdnImageUrl}/search/triangle_down_light.png` | imageToHttps" v-if="qs !== 'priceDesc'" />
              <img alt :src="`${$cdnImageUrl}/search/triangle_down_dark.png` | imageToHttps" v-if="qs === 'priceDesc'" />
            </div>
          </div>
          <div class="bar-line" v-if="currentIndex === index" />
        </div>
        <div class="bar-item" @click="changeStock()">
          <div class="bar-text" :class="{'active': chooseStock}">
            仅看有货
          </div>
        </div>
        <div style="display: flex; flex-direction: row;">
          <div class="vertical-line" />
          <img class="icon-filter" :src="prodShowType === 0 ? singleIcon : multipleIcon" @click="changeShowType" />
        </div>
      </div>
    </van-sticky>
    <common-empty v-if="finished && productList.length === 0 && !IdentifyText" />
    <identity-tip v-if="finished && productList.length === 0 && IdentifyText" :info="IdentifyText"></identity-tip>
    <van-list :finished="finished" :immediate-check="false" @load="onLoad" finished-text="没有更多啦~" v-model="loading" v-if="productList.length === 0 ? false : true">
      <public-product-list :productList="productList" :switchStyle='prodShowType === 0' :visitStore="storeId" @handleGoodClick="handleGoodClick" />
      <template slot="finished">
        <want-btn :type="prodShowType === 0?'white':'gray'"></want-btn>
      </template>
    </van-list>
  </div>
</template>
<script>
import publicProductList from '@/components/publicProductList';
import commonEmpty from '../commonEmpty';
import identityTip from '../identity-tip';
import { mapGetters } from 'vuex';
import wantBtn from '@/components/want-btn/index'
import {
  commonLoadItemNew,
  getPriceByProductList,
} from '@/http/service/shop';
const localData = {
  // pageIndex: 1,
  pageSize: 20,
};
const TABTYPE1 = [
  {
    tabName: '销量',
    qs: 'saleDesc',
    filterData: '',
  },
  {
    tabName: '价格',
    qs: 'price', // price、priceDesc
    filterData: '',
  },
];
/** 千店千面 底部tab 全部商品 首页精选商品tab 公用 */
export default {
  props: {
    // fromType: {
    //   type: Number,
    //   default: 0, // 0 - 底部商品tab  1 - 店铺首页商品bar 2- 首页精选bar底部商品
    // },
    storeId: {
      type: String,
      default: '',
    },
    shareId: {
      type: String,
      default: '',
    },
    isSelected: {
      type: Boolean,
      default: false,
    },
    isBottomBar: {
      type: Boolean,
      default: false,
    },
    offsetTop: {
      type: String,
      default: '1.1rem'
    },
    from: { // 1:精选底部瀑布流
      type: Number,
      default: null
    },
  },
  components: {
    publicProductList,
    commonEmpty,
    identityTip,
    wantBtn
  },
  data () {
    return {
      tabs: TABTYPE1,
      singleIcon: `${this.$cdnImageUrl}/search/layoutSingle_new.png`,
      multipleIcon: `${this.$cdnImageUrl}/search/layout_new.png`,
      priceSort: false,
      currentIndex: 0,
      prodShowType: 0, // 0-单列 1-双列
      productList: [],
      finished: false,
      loading: true,
      filterData: 'hasStock@all@0@',
      qs: 'saleDesc', // 销量优先
      chooseStock: true, // 仅看有货选中
      pageIndex: 1,
      IdentifyText: '' // 用户身份下单提示文本
    }
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  watch: {
    // location(newVal) {
    //   if (!this.loading) {
    //     this.pageIndex = 1;
    //     this.getProductList();
    //   }
    // },
    storeId: {
      handler (newVal) {
        this.pageIndex = 1;
        this.getProductList();
      },
      immediate: true,
    },
    mId(newVal) {
      if (!this.loading) {
        this.pageIndex = 1;
        this.getProductList();
      }
    }
  },
  methods: {
    changeTab (index) {
      if (this.currentIndex !== index) {
        this.currentIndex = index;
        this.qs = this.tabs[index].qs;
      } else if (this.tabs[index].tabName.includes('价格')) {
        this.priceSort = !this.priceSort;
        this.qs = this.priceSort ? 'priceDesc' : 'price';
      }
      this.pageIndex = 1;
      this.getProductList();
    },
    changeStock () {
      this.chooseStock = !this.chooseStock;
      this.pageIndex = 1;
      this.filterData = this.chooseStock ? 'hasStock@all@0@' : '';
      this.getProductList();
    },
    changeShowType () {
      if (this.prodShowType === 0) {
        this.prodShowType = 1;
      } else {
        this.prodShowType = 0;
      }
    },
    onLoad () {
      this.pageIndex++;
      this.getProductList();
    },
    getProductList () {
      commonLoadItemNew({
        channal: 1, // 渠道，固定值 1
        cityId: this.location.cityId || '173',
        provinceId: this.location.provinceId || '16',
        districtId: this.location.districtId || '2450',
        streetId: this.location.townshipId || '12036596',
        visitStore: this.storeId, // 访问店铺id
        memberId: this.shareId, // 分享人id
        filterData: this.filterData,
        qs: this.qs,
        pageIndex: this.pageIndex,
        pageSize: localData.pageSize,
        productCateId: 0, // 分类
        fromType: 2,
        keyword: '',
      }).then(res => {
        if (res.success) {
          if (res.data.productList.length) {
            getPriceByProductList({
              channelFlag: 'mini', // 微商城渠道标识（固定值 mini）
              traceId: res.data.traceId,
            }).then(priceRes => {
              if (priceRes.success) {
                res.data.productList.forEach(item => {
                  priceRes.products.forEach(skuItem => {
                    if (item.sku === skuItem.sku) {
                      item.finalPrice = skuItem.lowestFlashPrice || skuItem.finalPrice
                      item.isLowestFlash = !!skuItem.lowestFlashPrice
                      item.skku = skuItem.skku
                      item.commission = skuItem.commission
                      item.productActTerm = skuItem.productActTerm
                      item.priceType = skuItem.priceType
                      item.defaultPrice = skuItem.defaultPrice
                      item.promotion = skuItem.promotion
                      item.plusPrice = skuItem.plusPrice
                      item.clustersNum = skuItem.clustersNum
                      // item.productAct = skuItem.productAct
                    }
                  })
                })
                if (this.pageIndex === 1) {
                  this.productList = []
                }
                this.productList = this.productList.concat(res.data.productList)
                // console.log('商品列表:', this.productList);
              } else {
                this.$toast(priceRes.message)
              }
              this.loading = false;
              this.finished = res.data.productList.length !== localData.pageSize
            })
          } else {
            if (this.pageIndex === 1) {
              this.productList = []
              this.loading = false
              this.finished = true
            }
          }
        } else {
          this.productList = []
          this.loading = false
          if (res.errorCode === 'IdentifyNoSee') {
            // 用户身份问题接口失败 ，不toast提示，缺省位置文本提示
            this.IdentifyText = res.message
          } else {
            this.$toast(res.message)
          }
          this.finished = true
        }
      })
    },
    /**
     * 商品点击回调
    */
    handleGoodClick(item) {
      console.log(item)
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '店铺精选 商品瀑布流',
        modelCode: 'merc_water',
        site_number: item.index + '',
        itemCode: item.productId + '',
      })
    }
  },
}
</script>
